*,
*::after,
*::before,
html,
body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
}
a,
a:visited,
a:focus {
    text-decoration: none;
}
ul {
    list-style: none;
}
html {
    scroll-behavior: smooth;
}
@import "variables/variables";
@import "nav/nav";
@import "header/header";
@import "section-1/section-1";
